export default {
    data() {
        return {
            indicatorDots: true,
            autoplay: true,
            interval: 2000,
            duration: 500,
			circular:true,
			scrollTop: 0,
            old: {
                scrollTop: 0
            },
			banner_list:[],
        }
    },		
	created(){
		let _this=this;
	},
	mounted(){
		let _this=this;
		//baner
		_this.getBanner();	
	},
	methods: {
		//banner
		getBanner:function(){
			let _this=this;
			let data={
				id:12,
			};
			_this.$axios(_this.$baseUrl.banner,data).then(res =>{
				if(res.data.status==1){
					_this.banner_list=res.data.result.banner;
				}
			}).catch(error =>{
				
			})
		}
	}
}	
	
